import React from 'react';

const Navbar= (props)=> {
  
 
   
        
    return (
    <>

    <div style={styles.nav}>
   
        <div style={styles.cartIconContainer}>
     
            <img style={styles.cartIcon}alt="cart-icon" src="https://image.flaticon.com/icons/png/128/833/833314.png"/>
            <span style={styles.cartCount}>{props.count}</span>
        </div>
        </div>
    </>
    );
  }

const styles ={
    
cartIcon:{
    height:42,
 marginRight:155
},

nav:{
height:70,
background:'#4267b2',
display:'flex',
justifyContent:'flex-end',
alignItems:'center'
},
cartIconContainer:{
    position:'relative'
},
cartCount:{
    background:'yellow',
    borderRadius: '50%',
    padding: '4px 8px',
    position:'absolute',
    right:133,
    top:-9

}
};

export default Navbar;